import React from 'react';
import Background from '../components/Background';
import '../styles/UploadImageMenu.css';
import '../styles/Button.css';
import bWell from '../images/bWell.png';

export default ({
  onFileSelect,

  onSettingsButtonClicked,

  fileInputRef,
}) => (
  <Background color="darkgreen">
    <img className="UploadImageMenu__Logo" src={bWell} alt="bWell logo" />
    <label className="Button">
      Scan
      <input
        className="UploadImageMenu__Invisible"
        type="file"
        capture="environment"
        ref={fileInputRef}
        onChange={onFileSelect}
      />
    </label>
    <button
      className="Button"
      onClick={onSettingsButtonClicked}
    >
      Settings
    </button>
  </Background>
);



