import React from 'react';

const getClassName = (ingredientsToSearchFor, ingredient) => (
  ingredientsToSearchFor.some(flaggedIngredient => (
    flaggedIngredient.synonyms.some(flaggedSynonym => (
      ingredient.toLowerCase().includes(flaggedSynonym.toLowerCase())
    ))
  ))
    ? 'UserCorrectionMenu__FlaggedIngredient'
    : 'UserCorrectionMenu__UnflaggedIngredient'
);

export default (ingredients, searchableIngredients) => {
  const ingredientsToSearchFor = searchableIngredients
    .filter(entry => entry.shouldSearch);
  const ingredientArr = ingredients.split(',');
  const lastIngredientIndex = ingredientArr.length - 1;
  return ingredientArr.map((ingredient, ingredientIndex) => (
    <span className={getClassName(ingredientsToSearchFor, ingredient)}>
      {ingredient}
      {ingredientIndex === lastIngredientIndex ? null : ','}
    </span>
  ));
};



