import React from 'react';
import UploadImageMenu from './containers/UploadImageMenu';
import ScanPendingScreen from './containers/ScanPendingScreen';
import SettingsMenu from './containers/SettingsMenu';
import UserCorrectionMenu from './containers/UserCorrectionMenu';
import database from './database';
import imageCompression from 'browser-image-compression';
import stripNewlines from './stripNewlines';

const MAX_MB = 1;

export default class BWell extends React.Component {
  constructor() {
    super();

    this.state = {
      type: 'UPLOAD_IMAGE',
      ingredientsToSearchFor: database.map((entry) => ({
        ...entry,
        shouldSearch: localStorage[entry.name] === 'true',
      })),
    };

    [
      'onCorrectIngredients',
      'onFileSelect',
      'onHome',
      'onSettingsButtonClicked',
      'onToggleIngredient',
    ].forEach((methodName) => {
      this[methodName] = this[methodName].bind(this);
    });

    this.fileInputRef = React.createRef();
    this.textareaRef = React.createRef();
  }

  render() {
    switch (this.state.type) {
      case 'UPLOAD_IMAGE':
        return (
          <UploadImageMenu
            onFileSelect={this.onFileSelect}
            onSettingsButtonClicked={this.onSettingsButtonClicked}
            fileInputRef={this.fileInputRef}
          />
        );
      case 'SCAN_PENDING':
        return <ScanPendingScreen />;
      case 'SETTINGS_MENU':
        return (
          <SettingsMenu
            ingredientsToSearchFor={this.state.ingredientsToSearchFor}
            onHome={this.onHome}
            onToggleIngredient={this.onToggleIngredient}
          />
        );
      case 'USER_CORRECTION':
        return (
          <UserCorrectionMenu
            ingredients={this.state.ingredients}
            ingredientsToSearchFor={this.state.ingredientsToSearchFor}
            onCorrectIngredients={this.onCorrectIngredients}
            textareaRef={this.textareaRef}
          />
        );
      default:
        throw new TypeError('Unexpected state type: ' + this.state.type);
    }
  }

  onCorrectIngredients(ingredients) {
    this.setState({
      ingredients,
    });
    const textarea = this.textareaRef.current;
    textarea.style.height = textarea.scrollHeight + 'px';
  }

  onFileSelect() {
    const [file] = this.fileInputRef.current.files;
    if (file === undefined) {
      return;
    }
    if (!/\.(png|jpe?g)$/i.test(file.name)) {
      this.fileInputRef.current.value = null;
      return;
    }

    this.setState({
      type: 'SCAN_PENDING',
    }, () => {
      imageCompression(file, MAX_MB).then((compressedFile) => {
        const formData = new FormData();
        formData.append('file', compressedFile);
        formData.append('apikey', '43cfbc818988957');
        fetch(
          'https://api.ocr.space/parse/image',
          {
            method: 'POST',
            body: formData,
          },
        )
          .then(response => response.json())
          .then((responseJson) => {
            if (responseJson.ParsedResults === undefined) {
              document.write(JSON.stringify(responseJson, null, 4));
              return;
            }
            const parsedText = responseJson.ParsedResults[0].ParsedText;
            this.setState({
              type: 'USER_CORRECTION',
              ingredients: stripNewlines(parsedText),
            }, () => {
              const textarea = this.textareaRef.current;
              textarea.style.height = textarea.scrollHeight + 'px';
            });
          });
      });
    });
  }

  onHome() {
    this.setState({
      type: 'UPLOAD_IMAGE',
    });
  }

  onSettingsButtonClicked() {
    this.setState({
      type: 'SETTINGS_MENU',
    });
  }

  onToggleIngredient(name, shouldSearch) {
    this.setState({
      ingredientsToSearchFor: this.state.ingredientsToSearchFor
        .map((entry) => (
          entry.name === name ? { ...entry, shouldSearch } : entry
        )),
    });
    localStorage[name] = shouldSearch;
  }
}



