import React from 'react';
import Background from '../components/Background';
import Header from '../components/Header';
import HomeButton from '../components/HomeButton';
import '../styles/SettingsMenu.css';

export default ({ ingredientsToSearchFor, onHome, onToggleIngredient }) => (
  <Background color="white">
    <Header>Settings</Header>
    <HomeButton onClick={onHome} />
    <div className="SettingsMenu__Body">
      {ingredientsToSearchFor.map(({ name, shouldSearch }) => (
        <div className="SettingsMenu__Field">
          <label className="SettingsMenu__CheckboxLabel" key={name}>
            {name}
            <input
              className="SettingsMenu__Checkbox"
              type="checkbox"
              checked={shouldSearch}
              onChange={(e) => onToggleIngredient(name, e.target.checked)}
            />
          </label>
        </div>
      ))}
    </div>
  </Background>
);



