import React from 'react';
import '../styles/UserCorrectionMenu.css';
import highlightIngredients from '../highlightIngredients';

export default ({
  ingredients,
  ingredientsToSearchFor,

  onCorrectIngredients,

  textareaRef,
}) => (
  <div className="UserCorrectionMenu">
    <textarea
      ref={textareaRef}
      className="UserCorrectionMenu__Textarea"
      value={ingredients}
      onChange={e => onCorrectIngredients(e.target.value)}
    />
    <div className="UserCorrectionMenu__ReadOnlyOverlay">
      {highlightIngredients(ingredients, ingredientsToSearchFor)}
    </div>
  </div>
);



